.ImageRecognition {
    width: 100%;
    height: 26vh;
    margin: 20px auto;
    padding: 5px;
    color: #fff;
    background: rgba(255, 255, 255, 0.25);
    -webkit-backdrop-filter: blur(10px) saturate(1.5);
    backdrop-filter: blur(10px) saturate(1.5);
    border-radius: 10px;
    text-align: center;
}
.ImageRecognition div {
    color: white;
    font-size: 18px;
    padding: 10px 0;
}
.ImageRecognition .text {
    margin-top: 0px;
}
.ImageRecognition button {
    width: 150px;
    padding: 5px 10px;
    margin: 0 30px;
    background: rgba(255,255,255,.25);
    backdrop-filter: blur(10px) saturate(1.5);
    border-radius: 10px;
    border: 2px solid rgba(255,255,255,.0);
    transition: .5s;
}
.ImageRecognition button:hover {
    border: 2px solid rgba(255,255,255,1);
}